export default {
	data() {
		return {
			title: 'Hello',
			obj:{
				account:'',
				captcha:'',
				password:''
			},
			headPortrait:''
		}
	},
	onLoad() {

	},
	methods: {
		getSettingMes() {
		                let _this = this;
		                uni.getSetting({
		                    success(res) {
		                        if (res.authSetting['scope.userInfo']) {
		                            // 用户信息已授权，获取用户信息
		                            uni.getUserInfo({
		                                success(res) {
											_this.headPortrait=res.userInfo.avatarUrl
											_this.userLogin()
		                                },
		                                fail() {
		                                    console.log("获取用户信息失败")
		                                }
		                            })
		                        } else if (!res.authSetting['scope.userInfo']) {    
		                            console.log("需要点击按钮手动授权")
		                        }
		                    },
		                    fail() {
		                        console.log("获取已授权选项失败")
		                    }
		                })
		            },
		            // 手动授权方法
		            onGotUserInfo(e) {
						let _this = this;
		                // 获取用户信息
		                uni.getUserInfo({
		                    // 获取信息成功
		                    success(res) {
								_this.headPortrait=res.userInfo.avatarUrl
								_this.userLogin()                       
		                    },
		                    fail() {
		                        console.log("获取用户信息失败");
		                    }
		                })
		            },
		toRegister() {
			uni.navigateTo({
				url: 'register'
			})
		},
		toRetypePassword(){
			uni.navigateTo({
				url: 'retypePassword'
			})			
		},
		userLogin(){
			let that =  this;
			this.$apiYZX.login(this.obj).then(res=>{
				if(res.data.code==200){
					let obj={
						token:res.data.data.token,
						userid:res.data.data.user.id,
						phone:res.data.data.user.phone,
						name:res.data.data.user.name,
						headPortrait:this.headPortrait,
						landOrgan:res.data.data.user.organUsers
					}
					
					uni.setStorage({
					key:'ddwb',
					data:obj,
					success() {
						uni.showToast({
						    title: '登录成功',
							icon:'success',
							success() {
							
							
								if(obj.landOrgan.length>0){
									uni.navigateTo({
									    url: '../plantManage/plantManage'
									});	
									that.$api.getBaseId({organId:obj.landOrgan[0].organ.id}).then(res=>{
										uni.setStorage({
											key:'baseId',
											data:res.data.data.id,
										})
									})
							
								}else{
									uni.navigateTo({
									    url: '../plantManage/chooseHandle'
								});
								}
								
							}
						})		
					}
					})
				}			
			})
		}
	}
}
